const renderCustomizedLabel = (props) => {
    const {
      x, y, width, height, value, data,
    } = props;
    const radius = 10;
  
    return (
      <g>
        <circle cx={x + width / 2} cy={y - radius} r={radius} fill="#8884d8" />
        <text x={x + width / 2} y={y - radius} fill="#fff" textAnchor="middle" dominantBaseline="middle">
          {data.วิชาหลัก + data.กิจกรรมพัฒนาผู้เรียน}
        </text>
      </g>
    );
  };

  const getIntroOfPage = (label) => {
    if (label === 'Page A') {
      return "Page A is about men's clothing";
    } if (label === 'Page B') {
      return "Page B is about women's dress";
    } if (label === 'Page C') {
      return "Page C is about women's bag";
    } if (label === 'Page D') {
      return 'Page D is about household goods';
    } if (label === 'Page E') {
      return 'Page E is about food';
    } if (label === 'Page F') {
      return 'Page F is about baby food';
    }
  };

  const CustomTooltip = ({ active, payload, label }) => {
    if (active) {
      return (
        <div className="custom-tooltip">
          <p className="label">{`${label} : ${payload[0].value}`}</p>
          <p className="intro">{getIntroOfPage(label)}</p>
          <p className="desc">Anything you want can be displayed here.</p>
        </div>
      );
    }
  
    return null;
  };

export default function StackChart({data}) {
  
    const [chartData, setChartData] = React.useState([]);
    const [chartReady, setChartReady] = React.useState(false);
    const classes = useStyles();

    const [totalHrs, setTotalHrs] = React.useState(0);
    const [totalSec, setTotalSec] = React.useState(0);

    const handleClickDownload = (event) => {
      window.open(event,'_blank');
  }
  
    useEffect(() => {
        // Run! Like go get some data from an API.
        //console.log(orderData);
        //console.log(teachingData);
        //console.log(plcData);
        var teachingData = [];
        var total = 0;
        var t = 0;
        if(data.length>0){
            data.sort();
            data.map((obj,index) => {
          var a = "".concat(parseInt(obj.teachingTerm)).concat("/").concat(parseInt(obj.teachingYear));
          var b = parseFloat(obj.teachingSubjectMajor);
          var c = parseFloat(obj.teachingSubjectOthers);
          teachingData.push({name: a,
                 คาบวิชาหลัก: b,
                 คาบกิจกรรมพัฒนาผู้เรียน: c,
                 amt: 2400});
            
          total = parseFloat(total) + parseFloat(b) + parseFloat(c);
          t = t+parseInt(obj.teachingSubjectMajor)+parseInt(obj.teachingSubjectOthers);
          });
          setChartData(teachingData);
          if(teachingData.length>0){
            setChartReady(true);
            setTotalHrs((((total*50)/60)/data.length).toFixed(2));
            setTotalSec((t/data.length).toFixed(2));
          }
        }
        console.log(teachingData);
      }, []);

  return (
    <div>
       <Typography gutterBottom variant="subtitle2" component="h6" color="textPrimary" style={{width: "100%"}}>
                    สรุปข้อมูลตารางสอนในแต่ละภาคการศึกษา
                  </Typography>
        
{chartReady  && data && (
<TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table" size="medium">
        <TableHead>
          <TableRow>
            <TableCell align="right" colSpan={8}>
          <BarChart
              width={300}
              height={300}
              data={chartData}
              margin={{
                top: 5, right: 5, left: 5, bottom: 5,
              }}
            >
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip />
              <Legend width={300} wrapperStyle={{ bottom: 0, right: 0, backgroundColor: '#f5f5f5', border: '1px solid #d5d5d5', borderRadius: 3, lineHeight: '40px' }} />
              <Bar dataKey="คาบวิชาหลัก" stackId="a" fill="#8884d8" />
              <Bar dataKey="คาบกิจกรรมพัฒนาผู้เรียน" stackId="a" fill="#82ca9d">

            </Bar>
            </BarChart>
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell rowSpan={2}>ภาคเรียนที่</TableCell>
            <TableCell colSpan={2} align="center">วิชาหลัก</TableCell>
            <TableCell colSpan={2} align="center">กิจกรรมพัฒนาผู้เรียน</TableCell>
            <TableCell colSpan={2} align="center">รวมทั้งสิ้น</TableCell>
            <TableCell rowSpan={2} align="center">เอกสารอ้างอิง</TableCell>
          </TableRow>
          <TableRow>
            <TableCell align="right">คาบ</TableCell>
            <TableCell align="left">ชั่วโมง</TableCell>
            <TableCell align="right">คาบ</TableCell>
            <TableCell align="left">ชั่วโมง</TableCell>
            <TableCell align="right">คาบ</TableCell>
            <TableCell align="right">ชั่วโมง</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {chartData.map((item,idx) => (
              data.map((obj,index) =>  (
                index==idx && (
            <TableRow key={idx}>
              <TableCell component="th" scope="row">
                {item.name}
              </TableCell>
              <TableCell align="right">{obj.teachingSubjectMajor}</TableCell>
              <TableCell align="left">{((parseInt(obj.teachingSubjectMajor)*obj.periodTime)/60).toFixed(2)}</TableCell>
              <TableCell align="right">{obj.teachingSubjectOthers}</TableCell>
              <TableCell align="left">{((parseInt(obj.teachingSubjectOthers)*obj.periodTime)/60).toFixed(2)}</TableCell>
              <TableCell align="right">{parseInt(obj.teachingSubjectMajor)+parseInt(obj.teachingSubjectOthers)}</TableCell>
              <TableCell align="left">{(((parseInt(obj.teachingSubjectMajor)+parseInt(obj.teachingSubjectOthers))*obj.periodTime)/60).toFixed(2)}</TableCell>
              
              <TableCell align="center">
                {obj.teachingTable.split(',').length > 0 && ( 
                            obj.teachingTable.split(',').map((item, idx) => 
                                <IconButton key={idx}  aria-label="share"
                                onClick={()=> {handleClickDownload(item)}}>
                                <InsertDriveFileIcon/>
                                </IconButton> 
                        ))
                }
            </TableCell>    
            </TableRow> )))
          ))}
          <TableRow>
            <TableCell colSpan={8} align="center">รวมชั่วโมงสอนเฉลี่ยต่อปีการศึกษา {parseFloat(totalSec)} คาบ  คิดเป็น {parseFloat(totalHrs)} ชั่วโมง</TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>

)}
    </div>
  );
}